/*
    redux action creator function to create new actions
    synchronized action: object {type: 'xxx', data: data}
    asynchronous action: function: dispatch => {}
 */
import {SET_HEAD_TITLE, RECEIVE_USER, RESET_USER} from "./action-type";
import {reqLogin} from "../api";
import {message} from "antd";
import storageUtils from "../utils/storageUtils";

/*set header title (sync)*/
export const setHeaderTitle = (headerTitle) => ({
    type: SET_HEAD_TITLE,
    data: headerTitle
});

/* receive user */
export const receiveUser = (user) => ({
    type: RECEIVE_USER,
    user: user
});

//logout
export const logout = () => {
    //delete user in localStorage
    storageUtils.removeUser();
    return {
        type: RESET_USER
    };
}


/*login action(Async)*/
export const login = (username, password) => {
    return dispatch => {
        /*
            execute async ajax request
         */
        reqLogin(username, password)
            .then(
                //succeed: dispatch successful action
                response => {
                    const result = response.data
                    if(result.status === 0) {
                        const user = result.data;
                        storageUtils.saveUser(user);
                        dispatch(receiveUser(user));
                    } else {
                        const msg = result.msg;
                        message.error(msg);
                    }

                }
            )
            .catch(
                //failed: dispatch failed action
                err => {
                    message.error("login failed! reason: " + err.message)
                }
            )


    }
}