/*
    redux component to create new state based on current state and action
 */


/*
    reducer used to manage header title
 */
import {combineReducers} from "redux";
import storageUtils from "../utils/storageUtils";
import {RECEIVE_USER, RESET_USER, SET_HEAD_TITLE} from "./action-type";


/*
    reducer used to manage Header information
 */
const iniHeadTitle = 'Home'
function headTitle(state = iniHeadTitle, action) {
    switch (action.type) {
        case SET_HEAD_TITLE:
            return action.data;

        default:
            return state
    }
}

/*
    reducer used to manage current user
 */
const iniUser = storageUtils.getUser()
function user(state = iniUser, action) {
    switch (action.type) {
        case RECEIVE_USER:
            return action.user;
        case RESET_USER:
            //console.log("reset user!");
            return {};
        default:
            return state;
    }
}

/*
    combine two reducers and export it
    state:
        {
            headTitle: 'Home',
            user: {current user}
        }
 */
export default combineReducers({
    headTitle,
    user
})